@tailwind base;
@tailwind components;
@tailwind utilities;
.webCamCaptureBtn{
  width: 40px;
  height: 40px;
  border: 2px solid black;
  border-radius: 50%;
  position: absolute;
  bottom: 40px;
  left: 50%;
  transform: translateX(-50%);
}
.closeAll{
  width: 20px;
  height: 20px;
  position: relative;
  background-color: transparent;
}
.closeAll::after,
.closeAll::before{
  content: "";
  width: 100%;
  height: 2px;
  position: absolute;
  background-color: white;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}
.closeAll::after{
  transform: rotate(45deg);
}
.closeAll::before{
  transform: rotate(-45deg);
}
.ReactCrop__child-wrapper,
.video{
  position: absolute !important; 
  right: 0 !important; 
  bottom: 0 !important;
  min-width: 100% !important; 
  min-height: 100% !important;
  width: auto !important; 
  height: auto !important; 
  background-size: cover !important;
  overflow: hidden !important;
  max-width: unset !important;
}

/* banner  */
.searchForm::after{
  content: "";
  position: absolute;
  z-index: 10;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
    backdrop-filter: blur(10px);
    background: rgba(255, 255, 255, 0.1);
