 }
 
/* react calendar  */
.react-calendar{
  width: 100% !important;
  background-color: #f7f7f7 !important;
  border: none !important;
  border-radius: 8px !important;
  padding: 16px !important;
}
.react-calendar__navigation button{
min-width: 35px !important;
/* opacity: 0; */
}
.react-calendar__navigation button:enabled:hover{
  background-color: transparent !important;
}
.react-calendar__navigation__arrow{
opacity: 0;
}
.react-calendar__navigation__arrow{
  font-size: 26px !important;
  color: gray !important;
  background-color: white !important;
  width: 35px !important;
  height: 35px !important;
  transition: all 0.5s !important;
}
.react-calendar__navigation__label__labelText{
  font-size: 12px;
}
.react-calendar__navigation__arrow:hover{
  background:var(--mainColor) !important;
  color: white !important;
}
.react-calendar__navigation__prev2-button,
.react-calendar__navigation__next2-button{
  display: none !important;
}
.react-calendar__navigation{
  align-items: center !important
}
.react-calendar__navigation__label{
  line-height: 35px !important;
  font-weight: 500 !important;
}
abbr:where([title]){
  text-decoration: none !important;
  font-weight: 600 !important;
  font-size: 10px;
}

.react-calendar__tile--now{
color: black !important;
}
.react-calendar__month-view__days__day--weekend{
  color: black !important;
}
.react-calendar__month-view__days__day--neighboringMonth {
  color: #757575 !important;
}
.react-calendar__tile:disabled{
  opacity: 0.5;
  cursor: not-allowed;
}
.react-calendar__tile:disabled:hover{
  background-color: var(--mainColorLighter);
  
}
.react-calendar__navigation button:disabled {
  cursor: not-allowed;
}
.react-calendar__tile{
  font-size: 10px !important;
  line-height: 10px !important;
  font-weight: 500 !important;
}
.react-calendar__tile:hover,
.react-calendar__tile:focus,
.react-calendar__tile--active{
  color: white !important;
  background: var(--mainColor) !important ;
  border-radius: 8px !important;
  border-radius: 8px !important;
}
.nbControler svg path{
  transition: all 0.3s;
  }
  .nbControler svg:hover path{
fill: var(--mainColor);
}
.searchBottom{
  opacity: 0;
  pointer-events: none;
  top: calc(100% + 20px);
  transition: top 0.3s ease;
  border-top: 5px solid var(--mainColor);
  background-color: white;
  display: flex;
  flex-direction: column;
  box-shadow: 0 10px 10px rgba(0,0,0,0.1);
}
.searchTopContent{
  height: 100%;
  width: 100%;
}
.openedDropDown .searchBottom{
  opacity: 1;
  pointer-events: all;
  top: calc(100% + 10px);
}
.searchBottom.hide{
  opacity: 0;
  pointer-events: none;
}
.openedDropDown .searchBottom.show{
  opacity: 1;
  pointer-events: all;
}
.searchItem::after{
  content: "";
   position: absolute;
   width: 1.5px;
   height: 66%;
   background-color: #0000004f;
   z-index:29;
   right: 0;
   top: 50%;
   transform: translatey(-50%);
}
.searchItem:first-child:after{
  width: 1.5px;
}
.searchItem:nth-child(4)::before,
.searchItem:nth-child(4):after{
  width: 0;
}
/* voyage disponible */
.disponibleVoyages{
  opacity: 0;
  pointer-events: none;
  position: absolute;
  top: calc(100% + 40px);
  transition: top 0.3s ease;
}
.disponibleVoyages.show{
  opacity: 1;
  pointer-events: all;
  top: calc(100% + 20px);
}
.row::after{
content: "";
position: absolute;
width: 10px;
height: 10px;
background: var(--lightGray);
z-index: 10;
border: 1px solid black;
border-right-width: 2px;
border-bottom-width: 2px;
}
.row-1::after{
  left: 50%;
  border-radius: 50%;
  transform: translateX(-50%);
  top: 0;
}
.row-2::after{
  right: 50%;
  transform: translateX(50%);
  bottom: 0;
}
.voyageList::-webkit-scrollbar{
width: 6px;
}
.voyageList::-webkit-scrollbar-thumb{
  background-color: rgba(0, 0, 0, 0.225);
  border-radius: 15px;
}
.voyageItem .btnAction{
  transition: all .3s ;
}
.voyageItem .btnAction:hover{
  box-shadow: 0px 0 10px 0px var(--mainColor);
}
